import { Component } from '@angular/core';
import {ViewController, IonicPage, NavController, NavParams,AlertController, } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../providers';



@IonicPage()
@Component({
  selector: 'page-cursosview-html',
  templateUrl: 'cursosview-html.html',
})
export class CursosviewHtmlPage {

  titulox: any;
  linkx: any;
  html:any;
  nota:any;
  idcierre:any;
  items: any=[];   
  seguimiento:any;

  constructor(public viewCtrl: ViewController,public alertCtrl: AlertController,public regService: Auth,public navCtrl: NavController, public navParams: NavParams,public msj: NotificationProvider) {
  }
   
  ionViewDidLoad() {
    console.log('ionViewDidLoad MensajeviewPage');
    this.titulox=this.navParams.data.titulo;
    this.seguimiento=this.navParams.data.seguimiento;
    this.idcierre=this.navParams.data.idcierre;
  //  this.linkx=this.navParams.data._id;
this.html=this.navParams.data.html
   /*    
    this.regService.getReg(this.navParams.data.record._id + '/'+ this.navParams.data.record.idempresa + '/' + this.navParams.data.record2,'plantillads').then((data) => {
 
      this.items=data;
     
  }, (err) => {
    this.msj.showError3('Error',err.error);
  });
*/
    
  }

  
  rechazar() { 



    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {

          let prompt = this.alertCtrl.create({
            title:'Descripcion del rechazo ' ,
            
            inputs: [
              {
                name: 'nota'
              }
            ],
            buttons: [
              {
                text: 'Cancelar'
              },
              {
                text: 'Aceptar',
                handler: todo => {
                  
       
                      if(todo){
                        this.nota=todo.nota
       
                        let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
          buttons: [
            {text: 'Si',
              handler: () => {
       
              
    let   options       : any	     = { 
      id : this.idcierre,
      estado:'Asignado',
      motivo3:this.nota
      };
       this.regService.createRegsinmsg('actualiza',options,'participa3s').then((result) => {
      
           this.msj.showError3('Exito','Seguimiento rechazado exitosamente');
           this.viewCtrl.dismiss();
     }, (err) => {  
      
       this.msj.showError3('Error',err.error);
     });  
           
         
              }
            },
            {
              text: 'No',  handler: () => { }
            }
          ]
        });
        alert.present();
      
      
                       
       
                      }
       
       
                }
              }
            ]
          });
       
          prompt.present();


    
        



      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
  }
  
  cerrar() { 

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {
          
    let   options       : any	     = { 
      id : this.idcierre,
      estado:'Cerrado'
      };
       this.regService.createRegsinmsg('actualizacierre',options,'participa3s').then((result) => {
      
           this.msj.showError3('Exito','Seguimiento cerrado exitosamente');
           this.viewCtrl.dismiss();
     }, (err) => {  
      
       this.msj.showError3('Error',err.error);
     });    
        
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
  }
  

  dalink(op) { 
    //this.msj.showError3('Link QR',op);
    this.regService.abrelinkinterno3(this.linkx,'_blank','',0)
    
  }

  
  cancel() {
    this.viewCtrl.dismiss();
  }

}



